#clientes-header {
  color: #fff;
  position: relative;
  background: url(/storage/img/clientes-fondo.jpg) center top no-repeat scroll;
}

@media (max-width: 767px) {
  #clientes-header {
    background-size: 100% 230px;
    -webkit-background-size: 100% 230px;
    -moz-background-size: 100% 230px;
    -o-background-size: 100% 230px;
    height: 260px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  #clientes-header {
    background-size: 100% 350px;
    -webkit-background-size: 100% 350px;
    -moz-background-size: 100% 350px;
    -o-background-size: 100% 350px;
    height: 400px;
  }
}

@media (min-width: 991px) {
  #clientes-header {
    background-size: 100% 450px;
    -webkit-background-size: 100% 450px;
    -moz-background-size: 100% 450px;
    -o-background-size: 100% 450px;
    height: 550px;
  }
}

#clientes-header-logos {
  position: absolute;
}

@media (max-width: 767px) {
  #clientes-header-logos {
    bottom: 5px;
  }
}

@media (min-width: 767px) {
  #clientes-header-logos {
    bottom: 10px;
  }
}

#clientes-title h2 {
  font-weight: 700;
}

@media (max-width: 767px) {
  #clientes-title h2 {
    font-size: 16px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  #clientes-title h2 {
    font-size: 24px;
  }
}

@media (min-width: 991px) {
  #clientes-title h2 {
    font-size: 28px;
  }
}

#clientes-title h3 {
  font-weight: 700;
  margin-top: 5px;
}

@media (max-width: 767px) {
  #clientes-title h3 {
    font-size: 12px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  #clientes-title h3 {
    font-size: 20px;
  }
}

@media (min-width: 991px) {
  #clientes-title h3 {
    font-size: 24px;
  }
}

#armatura-logo {
  text-align: center;
}

@media (max-width: 767px) {
  #armatura-logo {
    padding-top: 15px;
    padding-bottom: 15px;
  }
}

@media (max-width: 767px) {
  #armatura-logo img {
    height: 60px;
    width: auto;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  #armatura-logo img {
    padding-top: 10px;
    height: 90px;
    width: auto;
  }
}

@media (min-width: 991px) {
  #armatura-logo img {
    padding-top: 20px;
    height: 120px;
    width: auto;
  }
}

.clientes-logo {
  text-align: center;
}

@media (max-width: 767px) {
  .clientes-logo img {
    height: 75px;
    width: 75px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  .clientes-logo img {
    height: 160px;
    width: 160px;
  }
}

@media (min-width: 991px) and (max-width: 1199px) {
  .clientes-logo img {
    height: 210px;
    width: 210px;
  }
}

@media (min-width: 1199px) {
  .clientes-logo img {
    height: 260px;
    width: 260px;
  }
}

[class*=" imghvr-"], [class^="imghvr-"] {
  background-color: #2d2d2d;
  cursor: pointer;
}

[class*=" imghvr-"] figcaption, [class^="imghvr-"] figcaption {
  background-color: #2d2d2d;
  color: #fff;
  padding: 0px;
}

[class*=" imghvr-"] h3, [class^="imghvr-"] h3 {
  font-weight: 300;
}

@media (max-width: 767px) {
  [class*=" imghvr-"] h3, [class^="imghvr-"] h3 {
    font-size: 11px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  [class*=" imghvr-"] h3, [class^="imghvr-"] h3 {
    font-size: 18px;
  }
}

@media (min-width: 991px) {
  [class*=" imghvr-"] h3, [class^="imghvr-"] h3 {
    font-size: 20px;
  }
}

[class*=" imghvr-"] p, [class^="imghvr-"] p {
  font-weight: 300;
}

@media (max-width: 767px) {
  [class*=" imghvr-"] p, [class^="imghvr-"] p {
    font-size: 10px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  [class*=" imghvr-"] p, [class^="imghvr-"] p {
    font-size: 16px;
  }
}

@media (min-width: 991px) {
  [class*=" imghvr-"] p, [class^="imghvr-"] p {
    font-size: 18px;
  }
}

@media (max-width: 767px) {
  .row-logos {
    margin-top: 10px;
    margin-bottom: 10px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  .row-logos {
    margin-top: 15px;
    margin-bottom: 15px;
  }
}

@media (min-width: 991px) {
  .row-logos {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}

@media (max-width: 767px) {
  .slider-clientes img {
    height: 80px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  .slider-clientes img {
    height: 120px;
  }
}

@media (min-width: 991px) {
  .slider-clientes img {
    height: 160px;
  }
}

ul.lSPager.lSpg {
  display: none;
}

.cS-hidden {
  height: 1px;
  opacity: 0;
  filter: alpha(opacity=0);
  overflow: hidden;
}

