    padding-bottom: 5px;
    font-size: 20px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  .logos-externos h2 {
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 26px;
  }
}

@media (min-width: 991px) {
